<template>
  <div>
    <el-row>
      <el-page-header @back="goBack" content="实时数据"> </el-page-header>
    </el-row>
    <el-row>
      <el-col :span="24">
        <el-form :inline="true" class="device-list-charts">
          <el-form-item label="当前活动数量">
            <span>{{onlineDeviceCount}}</span>
          </el-form-item>
          <el-form-item label="当前离线设备总数">
            <span>{{offlineDeviceCount}}</span>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
    <el-row>
      <el-col class="box-card" :span="4" v-for="dev in deviceMap" :key="dev[0]">
        <el-card>
          <div slot="header" class="clearfix">
            <!-- <el-checkbox v-model="dev[1].checked"></el-checkbox> -->
            <span>设备:{{ dev[0] }}</span>
          </div>
          <div v-for="tag in dev[1]" :key="tag.tagId" class="text item">
            {{ tag.tagId }}
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import mqtt from "mqtt";
import deviceApi from "@/api/device";
export default {
  name: "RealtimeData",
  data() {
    return {
      client: null,
      deviceMap: new Map(),
      onlineDeviceCount:0,
      offlineDeviceCount:0,
    };
  },
  created() {
  },
  mounted() {
    this.init();
    this.mqttInit('116.204.171.194',61614,'admin','admin');
  },
  methods: {
    goBack() {
      window.history.back();
    },
    init(){
      deviceApi.getDeviceStatistics().then((response) => {
        if (response.data) {
          this.onlineDeviceCount = response.data.onlineDevice;
          this.offlineDeviceCount = response.data.offlineDevice;
        }
      });
    },
    mqttInit(serverIp,serverPort,username,password) {
      let root = this;
      const options = {
        connectTimeout: 40000,
        clientId: "realtime-data-client",
        username: username,
        password: password,
        clean: true,
      };
      let mqTTUrl = 'ws://'+serverIp+':'+serverPort+'/mqtt';
      this.client = mqtt.connect(mqTTUrl, options);
      this.client.on("connect", (e) => {
        console.log("连接成功！！！");
        this.client.subscribe("personpositon/+/tags/+", { qos: 0 }, (error) => {
          if (!error) {
            console.log("订阅成功");
          } else {
            console.log("订阅失败");
          }
        });
      });
      // 接收消息处理
      this.client.on("message", (topic, message) => {
        let tag = JSON.parse(message.toString());
        console.log("收到来自", topic, "的消息", tag);
        if(root.deviceMap.has(tag.deviceCode)==false){
          root.deviceMap.set(tag.deviceCode,[]);
        }
        let tags = root.deviceMap.get(tag.deviceCode);
        tags.splice(0, 0, tag);
        if(tags.length>5){
          tags.pop();
        }
        root.$forceUpdate();
      });
    },
  },
};
</script>
<style>
.device-list-charts {
  text-align: left;
  margin-top: 30px;
}
.box-card {
  padding: 5px;
}
</style>



