import request from '@/utils/request.js';

const deviceApi = {
    /**
     * 获取设备列表
     * @param {*} params 
     */
    getDevices(params) {
        return request.get('devices/person-position-devices/list',{params});
    },
    /**
     * 获取设备统计
     * @param {} params 
     */
    getDeviceStatistics(params) {
        return request.get('devices-count',{params});
    },
}
export default deviceApi;


