import Vue from 'vue'
import Router from 'vue-router'
import DeviceList from '../view/DeviceList'
import RealtimeData from '../view/RealtimeData'
import HistoryData from '../view/HistoryData'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'DeviceList',
      component: DeviceList,
      meta: {
        title: '人员定位-设备列表'
      }
    },
    {
      path: '/realtime',
      name: 'RealtimeData',
      component: RealtimeData,
      meta: {
        title: '人员定位-实时数据'
      }
    },
    {
      path: '/history',
      name: 'HistoryData',
      component: HistoryData,
      meta: {
        title: '人员定位-历史数据'
      }
    }
  ]
})



